      life: 
        ["Артур Аляксандравіч Клінаў (5 верасня 1965 Мінск) — беларускі мастак акцыяніст дызайнер фатограф літаратар сцэнарыст галоўны рэдактар часопіса «pARTisan» (2002). Яркі прадстаўнік неафіцыйнага нефармальнага мастацтва авангардыст канцэптуальны мастак які ўпершыню заявіў пра сябе ў канцы 1980-х у Беларусі. Аўтар урбаністычна-культурнай канцэпцыі «Горад Сонца» — захавання архітэктурнай унікальнасці цэнтральнай часткі Мінска (Беларусь). ", "З’яўляецца прыкладам універсальнай творчай асобы якая развіваючы цікавую мастацкую ідэю здольна рэалізаваць навацыю ў любой мастацкай форме (традыцыйны жывапіс фатаграфія саламяная скульптура перформанс інсталяцыя відэа літаратура) з выкарыстаннем розных матэрыялаў і прадметаў рэальнасці[3]. ", "Беларускі культуролаг Максім Жбанкоў ахарактарызваў Артура Клінава як беларускага прадаўжальніка наватарства Эндзі Уорхала і Эміра Кустурыцы"],
